import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, FormArray, Validators } from '@angular/forms';
import { SharedService } from 'src/app/shared/shared.service';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';

@Component({
  selector: 'app-consignment',
  templateUrl: './consignment.component.html',
  styleUrls: ['./consignment.component.css']
})
export class ConsignmentComponent implements OnInit {


  dtypes: string[] = ['Direct delivery', 'Shead delivery'];
  vendorList;
  customerList;
  filteredId;
  consignform = this.fb.group({
    consignor: new FormControl('', Validators.required),
    consignee: new FormControl('', Validators.required),
    deliverytype: new FormControl('', Validators.required),
    additonal_charges: new FormControl('', Validators.required),
    handling_charges: new FormControl('', Validators.required),
    statiscal_charges: new FormControl('', Validators.required),
    other_charges: new FormControl('', Validators.required),
    total_cost: new FormControl('', Validators.required),
    goods:  this.fb.array([
        this.fb.group({
        description: new FormControl('', Validators.required),
        capacity: new FormControl('', Validators.required),
        price: new FormControl('', Validators.required),
        frieght_charges: new FormControl('', Validators.required),
      })
    ]),
    STATUS: new FormControl('', Validators.required),
    current_location: new FormControl('', Validators.required),
    current_place: new FormControl('', Validators.required),


  });

  get goods() {
    return this.consignform.get('goods') as FormArray;
  }

  constructor(private fb: FormBuilder,private sharedService: SharedService) { 


  }

  ngOnInit() {
    this.sharedService.sendGetVendor().subscribe((data: any[])=>{
      console.log(data);
      this.vendorList = data;
    }); 
    this.sharedService.sendGetCustomer().subscribe((data1: any[])=>{
      console.log(data1);
      this.customerList = data1;
    }); 
  }
  submit(){
    console.log(this.consignform.value);
  }

  addGoods() {
    return new FormGroup ({
      description: new FormControl('', Validators.required),
      capacity: new FormControl('', Validators.required),
      price: new FormControl('', Validators.required),
      frieght_charges: new FormControl('', Validators.required),
    })
  }
}
