import { Component, OnInit } from '@angular/core';
import { SharedService } from 'src/app/shared/shared.service';
import { Router } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';


@Component({
  selector: 'app-manage-customer',
  templateUrl: './manage-customer.component.html',
  styleUrls: ['./manage-customer.component.css']
})
export class ManageCustomerComponent implements OnInit {

  customerList = [];
  displayedColumns: string[] = ['custname', 'phone', 'email','action'];
  dataSource;
  constructor(private sharedService : SharedService, private router: Router,private http: HttpClientModule) { }

  ngOnInit() {
    this.customerList = this.sharedService.customerList;
    this.sharedService.sendGetCustomer().subscribe((data: any[])=>{
      console.log(data);
      //this.customerList = data;
    }); 
    this.dataSource = this.customerList;
  }

  addCustomer() {
    this.router.navigate(['/customerdetail']);
  }

   editCustomer(el) {
    this.router.navigate(['/customerdetail/', el.custname]);
   }

  

}
