import { Component, OnInit, HostListener } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  links = [
    { 
      name: "Customer", url: "/customer"
     }, {
       name: "Vehicle", url: "/vehicle"
     }, {
       name: "Vendor", url: "/vendor"
     }, {
       name: "Driver", url: "/driver"
     }, {
      name: "Consignment", url: "/consignment"
     }, {
       name: "Trip", url: "/customer"
     }, {
       name: "Logout", url: ""
     }];
  screenHeight: number;
  screenWidth: number;
  menuToggle: boolean = true;
  menulistbanner: boolean;

  constructor(private router: Router) { 
    this.onResize();
  }

  

  @HostListener('window:resize', ['$event'])
   onResize(event?) {
   this.screenHeight = window.innerHeight;
   this.screenWidth = window.innerWidth;
   console.log(this.screenWidth);
   if(this.screenWidth<500){
     console.log(this.menuToggle);
     this.menuToggle = false;
     this.menulistbanner = false;
   } else {
    this.menuToggle = true;
    this.menulistbanner = true;

   }
  }

  ngOnInit() {
   
  }

  toggleMenu(sidenav){
    this.menuToggle = !this.menuToggle;
  }

  showInfo(link){
    if(link.name=='Logout'){
      localStorage.clear();
      this.router.navigate([link.url]);
    } else{
      if(this.screenWidth<500){
        this.router.navigate([link.url]);
        this.menuToggle = false;
      } else {
        this.router.navigate([link.url]);
      }

    }
 
  }

  callLogout(){
    localStorage.clear();
    this.router.navigate(['']);
  }

}
